.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background: rgba(7,17,27,0.5);
}
.content-wrapper {
  position: relative;
  font-size: 0;
  padding: 24px 12px 18px 24px;
}
.content-wrapper .avatar {
  display: inline-block;
}
.content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
  font-size: 14px;
}
.content-wrapper .content .title {
  margin: 2px 0 8px 0;
}
.content-wrapper .content .title .brand {
  display: inline-block;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
  }
}
.content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  vertical-align: top;
}
.content-wrapper .content .description {
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px;
}
.content-wrapper .content .support .icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.content-wrapper .content .support .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .content-wrapper .content .support .icon.decrease {
    background-image: url("decrease_1@3x.png");
  }
}
.content-wrapper .content .support .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .content-wrapper .content .support .icon.discount {
    background-image: url("discount_1@3x.png");
  }
}
.content-wrapper .content .support .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .content-wrapper .content .support .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
  }
}
.content-wrapper .content .support .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .content-wrapper .content .support .icon.invoice {
    background-image: url("invoice_1@3x.png");
  }
}
.content-wrapper .content .support .icon.special {
  background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .content-wrapper .content .support .icon.special {
    background-image: url("special_1@3x.png");
  }
}
.content-wrapper .content .support .text {
  font-size: 10px;
  line-height: 12px;
}
.content-wrapper .support-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  height: 20px;
  line-height: 20px;
  border-radius: 14px;
  background-color: rgba(0,0,0,0.2);
  text-align: center;
}
.content-wrapper .support-count .count {
  vertical-align: top;
  font-size: 10px;
}
.content-wrapper .support-count .icon-keyboard_arrow_right {
  font-size: 10px;
}
.bulletin-wrapper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background-color: rgba(7,17,27,0.2);
}
.bulletin-wrapper .bulletin-title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  vertical-align: top;
  margin-top: 7px;
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
  }
}
.bulletin-wrapper .bulletin-text {
  vertical-align: top;
  margin: 0 4px;
  font-size: 10px;
}
.bulletin-wrapper .bulletin-text:after {
  content: " >";
  font-size: 10px;
  position: absolute;
  right: 10px;
  top: 0;
}
.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7,17,27,0.8);
}


