.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-descrease {
  display: inline-block;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-descrease .inner {
  background-image: url("cartDescrease@2x.png");
  background-size: 100% 100%;
  display: inline-block;
  width: 18px;
  height: 18px;
  color: #00a0dc;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .cartcontrol .cart-descrease .inner {
    background-image: url("cartDescrease@3x.png");
  }
}
.cartcontrol .cart-descrease.move-transition {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontrol .cart-descrease.move-transition .inner {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .cart-descrease.move-enter,
.cartcontrol .cart-descrease.move-leave {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartcontrol .cart-descrease.move-enter .inner,
.cartcontrol .cart-descrease.move-leave .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  line-height: 24px;
  width: 12px;
  height: 16px;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  padding-left: 12px;
  padding-right: 12px;
  text-align: center;
  font-size: 14px;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  background-image: url("cartAdd@2x.png");
  background-size: 100% 100%;
  line-height: 18px;
  margin: 6px;
  margin-bottom: 0;
  width: 18px;
  height: 18px;
  color: #00a0dc;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
  .cartcontrol .cart-add {
    background-image: url("cartAdd@3x.png");
  }
}


