.ratingselect .rating-type {
  margin: 0 18px;
  padding: 18px 0;
  position: relative;
  font-size: 0px;
}
.ratingselect .rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingselect .rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  border-radius: 2px;
  margin-right: 8px;
  color: rbg(77, 85, 93);
  font-size: 12px;
  background: rgba(0,160,220,0.2);
  line-height: 16px;
}
.ratingselect .rating-type .block.active {
  background: #00a0dc;
  color: #fff;
}
.ratingselect .rating-type .block .count {
  margin-left: 2px;
  font-size: 8px;
}
.ratingselect .rating-type .block.positive {
  background: rgba(0,160,220,0.2);
}
.ratingselect .rating-type .block.positive.active {
  background: #00a0dc;
}
.ratingselect .rating-type .block.negative {
  background: rgba(77,85,93,0.2);
}
.ratingselect .rating-type .block.negative.active {
  background: #4d555d;
}
.ratingselect .switch {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  font-size: 0;
}
.ratingselect .switch .icon-check_circle {
  font-size: 24px;
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-right: 4px;
  background: #93999f;
  border-radius: 50%;
  vertical-align: middle;
  color: #fff;
  text-align: center;
}
.ratingselect .switch .icon-check_circle.on {
  background: #f00;
}
.ratingselect .switch .text {
  display: inline-block;
  font-size: 12px;
  vertical-align: middle;
}


